<template>
    <div class="BoxBox" :style="{'padding-right': drawer === false? '60px':'400px'}">
        <div style="display: flex;">
        <el-input v-model="searchName" placeholder="搜索角色名" @input="searchByName" style="width: 200px; margin: 0 40px 0 25px; height: 35px; box-shadow: #7e282b16 0 0 30px 1px; --el-input-focus-border-color: #ac2914; --el-input-border-radius: 15px;"/>
        <div class="showRfBox">
            <span style="transition: .5s ease;position: absolute; height: 35px; width: 53%; background-color: #ac2914;border-radius: 20px;" :style="{'transform':isShowRf===0?'translateX(94%)':'translateX(0%)'}"></span>
            <div @click="changeShowRf(1)"><p :style="{'color':isShowRf===0?'#ac2914':'#f0ece5'}">台服</p></div>
            <div @click="changeShowRf(0)"><p :style="{'color':isShowRf===1?'#ac2914':'#f0ece5'}">日服</p></div>
        </div>
        <UpOrDownBar class="downOrUp" @onEmitIndex_uod="onEmitIndex_uod" :characterData_upOrDown="characterData_upOrDown"/></div>
        <div style="padding: 10px 25px; margin-top: 5px; background-color: #e7dbcf; border-radius: 15px;">
            <CheckBar @checkChange="choise" @checkedElementsChange="checkedElementsChange" :elements="elements" :textName="'el'" :num="0" :kind="'元素'"/>
            <CheckBar @checkChange="choise" @checkedElementsChange="checkedElementsChange" :elements="rarity" :textName="'text'" :num="1" :kind="'稀有度'"/>
            <CheckBarOnlyText @checkChange="choise" @checkedElementsChange="checkedElementsChange" :elements="isMagicText" :num="2" :kind="'属性'"/>
            <CheckBarOnlyText @checkChange="choise" @checkedElementsChange="checkedElementsChange" :elements="identityText" :num="4" :kind="'身份'"/>
            <CheckBar @checkChange="choise" @checkedElementsChange="checkedElementsChange" :elements="weapon" :textName="'wp'" :num="3" :kind="'武器'"/>
        </div>
        <div style="margin-top: 12px; margin-left: 10px; font-size: 14px; opacity: .8; color: #314656;">
            <p><span style="padding: 0 10px 0 0; opacity: .3;">——</span>{{ characterWillShow.length }} / {{ characterData.length }}<span style="padding: 0 0 0 10px; opacity: .3;">——</span></p>
        </div>
        <div style="margin-top: 2px;">
            <CharacterItem
                v-for="(item, index) in characterWillShow"
                :key="index"
                :character="item"
                @click="showTable(index)"
                :upOrDown_name="upOrDown_name"
                :onclickCharacterId="onclickCharacterId"
            />
        </div>
        <div style="margin-top: 5px; margin-left: 10px; font-size: 14px; opacity: .8; color: #314656;">
            <p><span style="padding: 0 10px 0 0; opacity: .3;">——</span>{{ characterWillShow_sq.length }} / {{ characterData_sq.length }}<span style="padding: 0 0 0 10px; opacity: .3;">——</span></p>
        </div>
        <div style="margin-top: 2px;margin-bottom: 15px;">
            <CharacterItem
                v-for="(item, index) in characterWillShow_sq"
                :key="index"
                :character="item"
                @click="showTable_sq(index)"
                :upOrDown_name="upOrDown_name"
                :onclickCharacterId="onclickCharacterId"
            />
        </div>
        <el-drawer
            :modal="false"
            v-model="drawer"
            :with-header="false"
            :close-on-click-modal="false"
            :lock-scroll="false"
            :size="380"
            :z-index="2"
            style="background-color: #fef8ef;"
        >
            <CharacterTable :character="tabling" :drawer="drawer" :isShowRf="isShowRf"/>
        </el-drawer>
    </div>
</template>



<script>
import characterData from '../assets/json/characterData.json'
import sq_characterData from '../assets/json/sq_characterData.json'
import CharacterTable from './CharacterTable'
import CharacterItem from './CharacterItem'
import CheckBar from './CheckBar'
import CheckBarOnlyText from './CheckBarOnlyText'
import UpOrDownBar from './UpOrDownBar'

export default {
    data() {
        return {
            isShowRf:0,
            characterData:characterData,
            characterData_sq:sq_characterData,
            characterWillShow:[],
            characterWillShow_sq:[],
            onclickCharacterId:-1,

            searchName:'',
            characterAfterSearch:[],
            characterAfterSearch_sq:[],

            elements:['h','f','s','y','g','a'],
            rarity:['r','sr','ssr'],
            isMagicText:['物理','魔法'],
            identityText:['魂友','神器'],
            weapon:['c','f','ft','g','j','jd','q','s','y'],
            isSz:[1,0],
            checkedElements:[[],[],[],[],[],[]],
            characterAfterChoise:[],
            characterAfterChoise_sq:[],

            upOrDown_name:-1,
            upOrDown_uod:-1,


            drawer:false,
            tabling:{},
        };
    },
    mounted(){
        this.checkedElements[0] = this.elements
        this.checkedElements[1] = this.rarity
        this.checkedElements[2] = this.isMagicText
        this.checkedElements[3] = this.weapon
        this.checkedElements[4] = this.identityText
        this.characterAfterSearch = this.characterData
        this.characterAfterSearch_sq = this.characterData_sq
        this.choise()
    },
    methods:{
        changeShowRf(e){
            this.isShowRf = e
            this.choise()
        },
        showTable(e){
            this.onclickCharacterId = this.characterWillShow[e].iconId
            if( this.characterWillShow[e].iconId === this.tabling.iconId || this.drawer === false){
                this.tabling = this.characterWillShow[e]
                this.drawer = !this.drawer
            }else{
                this.tabling = this.characterWillShow[e]
            }
        },
        showTable_sq(e){
            this.onclickCharacterId = this.characterWillShow_sq[e].iconId
            if( this.characterWillShow_sq[e].iconId === this.tabling.iconId || this.drawer === false){
                this.tabling = this.characterWillShow_sq[e]
                this.drawer = !this.drawer
            }else{
                this.tabling = this.characterWillShow_sq[e]
            }
        },
        choise(){
            this.characterAfterChoise = this.characterData.filter(item => {
                return (
                    this.checkedElements[0].includes(item.element)&&
                    this.checkedElements[1].includes(item.rarity)&&
                    this.checkedElements[2].includes((item.isMagic===1?'魔法':'物理'))&&
                    (this.checkedElements[3].includes(item.weapon[0])||this.checkedElements[3].includes(item.weapon[1]))&&
                    this.checkedElements[4].includes((item.isSQ===1?'神器':'魂友'))&&
                    item.isSz >= this.isShowRf
                );
            });
            this.characterAfterChoise_sq = this.characterData_sq.filter(item => {
                return (
                    this.checkedElements[0].includes(item.element)&&
                    this.checkedElements[1].includes(item.rarity)&&
                    this.checkedElements[2].includes((item.isMagic===1?'魔法':'物理'))&&
                    (this.checkedElements[3].includes(item.weapon[0])||this.checkedElements[3].includes(item.weapon[1]))&&
                    this.checkedElements[4].includes((item.isSQ===1?'神器':'魂友'))&&
                    item.isSz >= this.isShowRf
                );
            });
            this.showCharacterAfterSearchAndChoise()
        },
        checkedElementsChange(newElements,elNum){
            this.checkedElements[elNum] = newElements
        },
        searchByName(){
            this.characterAfterSearch = this.searchName === '' ? this.characterData : this.characterData.filter( item => { return item.name_CN.includes(this.searchName)})
            this.characterAfterSearch_sq = this.searchName === '' ? this.characterData_sq : this.characterData_sq.filter( item => { return item.name_CN.includes(this.searchName)})
            this.showCharacterAfterSearchAndChoise()
        },
        showCharacterAfterSearchAndChoise(){
            this.characterWillShow = this.characterAfterChoise.filter(item1 => this.characterAfterSearch.some(item2 => item1.iconId === item2.iconId))
            this.characterWillShow_sq = this.characterAfterChoise_sq.filter(item1 => this.characterAfterSearch_sq.some(item2 => item1.iconId === item2.iconId))
        },
        onEmitIndex_uod(e) {
            this.upOrDown_name = e['name']
            this.upOrDown_uod = e['uod']
        },
        characterData_upOrDown(upOrDown, attrNum){
            if( attrNum >= 0){
                upOrDown === 0 ? this.characterWillShow.sort((a, b) => b.attr[attrNum] - a.attr[attrNum]) : this.characterWillShow.sort((a, b) => a.attr[attrNum] - b.attr[attrNum])
                upOrDown === 0 ? this.characterWillShow_sq.sort((a, b) => b.attr[attrNum] - a.attr[attrNum]) : this.characterWillShow_sq.sort((a, b) => a.attr[attrNum] - b.attr[attrNum])
            }else{
                upOrDown === 0 ? this.characterWillShow.sort((a, b) => parseInt(a.iconId) - parseInt(b.iconId)) : this.characterWillShow.sort((a, b) => parseInt(b.iconId) - parseInt(a.iconId))
                upOrDown === 0 ? this.characterWillShow_sq.sort((a, b) => parseInt(a.iconId) - parseInt(b.iconId)) : this.characterWillShow_sq.sort((a, b) => parseInt(b.iconId) - parseInt(a.iconId))
            }
        },
    },
    components: {
        CharacterItem,
        CharacterTable,
        CheckBar,
        CheckBarOnlyText,
        UpOrDownBar,
    }
}
</script>


<style scoped>
.showRfBox{
    position: relative;
    margin-right: 35px;
    display: flex;
    align-items: center;
    height: 32px;
    border: 2px solid #7e282b;
    overflow: hidden;
    border-radius: 20px;
    box-shadow: #7e282b2f 0 0 30px 1px;
}
.showRfBox div{
    cursor: pointer;
    border-radius: 20px;
    position: relative;
    height: 100%;
    width: 50px;
}
.showRfBox div:nth-child(1){
    padding: 0 1px 0 6px;
}
.showRfBox div:nth-child(2){
    padding: 0 6px 0 1px;
}
.showRfBox p{
    position: absolute;transition: .5s ease;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    color: #e7dbcf;
    font-size: 14px;
    text-align: center;
    width: 50px;
}
.BoxBox{
    overflow: auto;
    max-height: 90vh;
    transition: .5s ease;
    padding: 0 60px 0 210px;
    margin-top: 20px;
}
.downOrUp{
    display: inline-block;
}
</style>
