<template>
    <div class="BoxBox" style="padding-right: 60px; margin-top: 0;">
        <div style="background-color: #e7dbcf;height: 100%; width: 100%; padding: 15px 15px; box-sizing: border-box; box-shadow: rgba(18, 18, 18, 0.16) 0 -10px 12px 5px; z-index: 3; position: relative;">
            <div class="oylTitle">
                <p class="title">日服活动与卡池日历</p>
                <div>
                    <p>*下图为日服活动日历，台服中活动的日期与排布会有略微出入，仅供参考</p>
                    <p>*标有角色头像的活动，该角色为该活动的赠送角色；标有角色头像的卡池，该角色为该卡池的封面角色</p>
                    <p>*常驻角色在首次UP后通常将在下个月的月末加入常驻池</p>
                </div>
            </div>
            <div id="gantt" class="gantt"></div>
        </div>
    </div>
</template>


<script>
import { gantt } from 'dhtmlx-gantt'; // 核心模块
import 'dhtmlx-gantt/codebase/dhtmlxgantt.css'; // 样式模块
import { data_org, data_char_org, dayPass } from '../assets/json/HDcalendar.js'
import characterData from '../assets/json/characterData.json'
import sq_characterData from '../assets/json/sq_characterData.json'

const colorMap = [
    '#9a60b4',//降临战
    '#5470c6',//武器活动
    '#ee6666',//剧情活动
    '#3db9d3',//周年活动
    '#3ba272',//特殊活动，节日等
]
function getDateAfter333Days(originalDate) {
    // 复制原始日期以避免修改原始日期
    var newDate = new Date(originalDate);

    // 将日期向后调整333天
    newDate.setDate(newDate.getDate() + dayPass);
    var year = newDate.getFullYear();
    var month = newDate.getMonth() + 1; // 月份从0开始，所以需要加1
    var day = newDate.getDate();

    // 返回年月日格式的字符串
    return year + "-" + (month < 10 ? "0" + month : month) + "-" + (day < 10 ? "0" + day : day);
}
function formatDate(dateString) {
    // 创建一个新的日期对象
    var date = new Date(dateString);

    // 提取年、月和日
    var year = date.getFullYear();
    var month = date.getMonth() + 1; // 月份从0开始，所以需要加1
    var day = date.getDate();

    // 返回年月日格式的字符串
    return year + "-" + (month < 10 ? "0" + month : month) + "-" + (day < 10 ? "0" + day : day);
}
export default {
    data() {
        return {
            currentDate:null,
        };
    },
    methods:{
    },
    mounted(){
        let data = JSON.parse(JSON.stringify(data_org));
        let data_char = JSON.parse(JSON.stringify(data_char_org));
        this.currentDate = new Date();

        // 将日期向前调整334天
        this.currentDate.setDate(this.currentDate.getDate() - dayPass);
        data.forEach(function(item) {
            item.color = colorMap[item.parent-1];
            if(item.id<=4){
                return
            }
            if(item.parent===4){
                item.color = colorMap[item.parent-1];
            }
            item.text = `<span style="vertical-align: middle;">${item.text}</span>`
            if(item.cid[0]!==-1){
                let textImg = ``
                for(let i=0;i<item.cid.length;i++){
                    textImg += `<div style="vertical-align: middle;height:95%;margin:0 3px;display:inline-block;position:relative;">
                        <img src="${require(`@/assets/icon/character/${item.cid[i].toString().padStart(4, '0')}.png`)}" style="height:100%;z-index:1;position:relative;">
                        <img src="${require(`@/assets/icon/others/${characterData[item.cid[i]-1].rarity}_bg.png`)}" style="height:100%;position:absolute;left:0;z-index:0;">
                        <img src="${require(`@/assets/icon/others/${characterData[item.cid[i]-1].rarity}_border.png`)}" style="height:100%;position:absolute;left:0;z-index:2;" title="${characterData[item.cid[i]-1].name_CN}">
                        <img src="${require(`@/assets/icon/others/el_${characterData[item.cid[i]-1].element}.png`)}" style="height:35%;width:35%;position:absolute;right:0;z-index:3;bottom:0;" title="${characterData[item.cid[i]-1].name_CN}">
                    </div>`
                }
                item.text += textImg
            }
            if(item.sid[0]!==-1){
                let textImg = ``
                for(let i=0;i<item.sid.length;i++){
                    textImg += `<div style="vertical-align: middle;height:95%;margin:0 3px;display:inline-block;position:relative;">
                        <img src="${require(`@/assets/icon/character/${(6000+item.sid[i]).toString()}.png`)}" style="height:100%;z-index:1;position:relative;">
                        <img src="${require(`@/assets/icon/others/${sq_characterData[item.sid[i]-1].rarity}_bg.png`)}" style="height:100%;position:absolute;left:0;z-index:0;">
                        <img src="${require(`@/assets/icon/others/${sq_characterData[item.sid[i]-1].rarity}_border.png`)}" style="height:100%;position:absolute;left:0;z-index:2;" title="${sq_characterData[item.sid[i]-1].name_CN}">
                        <img src="${require(`@/assets/icon/others/el_${sq_characterData[item.sid[i]-1].element}.png`)}" style="height:35%;width:35%;position:absolute;right:0;z-index:3;bottom:0;" title="${sq_characterData[item.sid[i]-1].name_CN}">
                    </div>`
                }
                item.text += textImg
            }
        });
        data_char.forEach(function(item) {
            if(item.id<=1000){
                return
            }
            if(item.text.includes('新实装'))
                item.color = '#314656';
            else
                item.color = '#314656d2';
            item.text = `<span style="vertical-align: middle;">${item.text}</span>`
            if(item.cid[0]!==-1){
                let textImg = ``
                for(let i=0;i<item.cid.length;i++){
                    textImg += `<div style="vertical-align: middle;height:95%;margin:0 3px;display:inline-block;position:relative;">
                        <img src="${require(`@/assets/icon/character/${item.cid[i].toString().padStart(4, '0')}.png`)}" style="height:100%;z-index:1;position:relative;">
                        <img src="${require(`@/assets/icon/others/${characterData[item.cid[i]-1].rarity}_bg.png`)}" style="height:100%;position:absolute;left:0;z-index:0;">
                        <img src="${require(`@/assets/icon/others/${characterData[item.cid[i]-1].rarity}_border.png`)}" style="height:100%;position:absolute;left:0;z-index:2;" title="${characterData[item.cid[i]-1].name_CN}">
                        <img src="${require(`@/assets/icon/others/el_${characterData[item.cid[i]-1].element}.png`)}" style="height:35%;width:35%;position:absolute;right:0;z-index:3;bottom:0;" title="${characterData[item.cid[i]-1].name_CN}">
                    </div>`
                }
                item.text += textImg
            }
            if(item.sid[0]!==-1){
                let textImg = ``
                for(let i=0;i<item.sid.length;i++){
                    textImg += `<div style="vertical-align: middle;height:95%;margin:0 3px;display:inline-block;position:relative;">
                        <img src="${require(`@/assets/icon/character/${(6000+item.sid[i]).toString()}.png`)}" style="height:100%;z-index:1;position:relative;">
                        <img src="${require(`@/assets/icon/others/${sq_characterData[item.sid[i]-1].rarity}_bg.png`)}" style="height:100%;position:absolute;left:0;z-index:0;">
                        <img src="${require(`@/assets/icon/others/${sq_characterData[item.sid[i]-1].rarity}_border.png`)}" style="height:100%;position:absolute;left:0;z-index:2;" title="${sq_characterData[item.sid[i]-1].name_CN}">
                        <img src="${require(`@/assets/icon/others/el_${sq_characterData[item.sid[i]-1].element}.png`)}" style="height:35%;width:35%;position:absolute;right:0;z-index:3;bottom:0;" title="${sq_characterData[item.sid[i]-1].name_CN}">
                    </div>`
                }
                item.text += textImg
            }
        });
        gantt.i18n.setLocale("cn");
        gantt.config.show_grid = false
        gantt.config.autoscroll = true
        gantt.config.show_links = false
        gantt.config.drag_resize = false
        gantt.config.drag_move = true
        gantt.config.readonly = true
        gantt.config.scale_unit = "month"; //按年显示
        gantt.config.step = 1;	//设置时间刻度的步长（X轴）
        gantt.config.date_scale = "%Y-%M";	//日期尺度按年
        gantt.config.xml_date = "%Y-%m-%d";
        gantt.config.scale_height = 60;
        gantt.config.column_width = 20;
        gantt.config.row_height = 45; 
        gantt.plugins({ marker: true, tooltip: true });
        gantt.templates.tooltip_text = function (start, end, task) {
            // console.log(task);
            return `<div><div style="height:35px;font-size:18px;font-weight:600;">${task.text}</div><br/><div style="font-size: 13px;margin-left:15px;margin-right:15px;">日服开始：${formatDate(start)}<br/>日服结束：${formatDate(end)}<br/>台服预计开始：${getDateAfter333Days(start)}<br/>台服预计结束：${getDateAfter333Days(end)}</div></div>`;
        }
        gantt.config.subscales = [
            {unit: "day", date: "%d"}
        ];
        gantt.clearAll();
        
        gantt.init(document.getElementById('gantt'));
        gantt.parse({data:data});

        gantt.init(document.getElementById('gantt'));
        gantt.parse({data:data_char});
        gantt.showDate(this.currentDate);
    }
}
</script>


<style scoped>
.BoxBox{
    overflow: auto;
    height: 90vh;
    transition: .5s ease;
    padding: 0 60px 0 210px;
    margin-top: 14px;
}
.gantt{
    height: 85%;
    overflow: hidden;
    box-sizing: border-box;
    box-shadow: #c2b7ac35 0 0 10px 2px;
}
.oylTitle{
    display: flex;
    align-items: center;
    color: #0d1422;
    padding-left: 15px;
    line-height: 22px;
    margin-bottom: 8px;
    box-sizing: border-box;
    font-size: 13px;
    position: relative;
}
.oylTitle div{
    margin-left: 50px;
    border-left: #0d14229f 2px solid;
    padding-left: 15px;
    border-radius: 3px;
}
.oylTitle span{
    display: inline-block;
    height: 10px;
    opacity: .85;
    width: 16px;
    margin-right: 8px;
    border: 1px #cecece solid;
    box-shadow: #c2b7ac35 0 0 10px 2px;
    background-color: #ffffff;
}
.title{
    cursor: default;
    background-color: #314656;
    display: inline-block;
    /* position: absolute;
    top: 50%;
    right: 15px;
    transform: translateY(-50%); */
    color: #f4f4f4;
    padding: 5px 12px;
    border-radius: 15px;
    margin-left: 8px;
}
</style>